import { createRouter, createWebHashHistory } from 'vue-router'
import {Toast} from 'vant'
const router = createRouter({
    history:createWebHashHistory(),
    routes:[
        {
            path:'/',
            component:()=> import('../views/home/myHome.vue')
        },
        {
            path:'/cart',
            component:()=> import('../views/cart/myCart.vue'),
            meta:{
                isAuth:true
            }
        },
        {
            path:'/order',
            component:()=> import('../views/order/myOrder.vue'),
            meta:{
                isAuth:true
            }
        },
        {
            path:'/mine',
            component:()=> import('../views/mine/myMine.vue'),
            meta:{
                isAuth:true
            }
        },
        {
            path:'/store',
            component:()=> import('../views/store/Store.vue')
        },
        {
            path:'/createOrder',
            component:()=> import('../views/createOrder/CreateOrder.vue'),
            meta:{
                isAuth:true
            }
        },
        {
            path:'/address',
            component:()=> import('../views/address/Address.vue'),
            meta:{
                isAuth:true
            }
        },
        {
            path:'/addressEdit',
            component:()=> import('../views/addressEdit/AddressEdit.vue'),
            meta:{
                isAuth:true
            }
        },
        {
            path:'/userInfoEdit',
            component:()=> import('../views/userInfoEdit/UserInfoEdit.vue'),
            meta:{
                isAuth:true
            }
        },
        {
            path:'/login',
            component:()=> import('../views/login/Login.vue')
        },
        {
            path:'/register',
            component:()=> import('../views/register/Register.vue')
        },
    ]
})
router.beforeEach((to,from,next)=>{
    if(to.meta.isAuth){
        //登录以后，会在localStorage里面 存储一个标识
        if(localStorage.isLogin === 'login'){
            next()
        }else{
            next('/login')
            Toast('请先去登录')
        }
    }else{
        next()
    }
})
export default router